<template>
  <div class="sidebar-tools">
    <ak-tabs class="padding">
      <ak-tab-pane label="">
        <h3></h3>
        <ak-form-item label="">
          <ak-input v-model="dataList.className" placeholder=""></ak-input>
        </ak-form-item>
        <template v-if="dataList.type==='tabs'">
          <h3></h3>
          <div v-for="(item,index) in dataList.columns" :key="index" class="flex">
            <ak-input v-model="item.label" placeholder=""></ak-input>
            <i class="icon-del" @click="_delTabsLabel(index)"></i>
          </div>
          <ak-button @click="_addTabsLabel" type="primary"></ak-button>
        </template>
        <template v-if="isFormControl">
          <ak-form-item label="">
            <ak-input v-model="dataList.name" placeholder=""></ak-input>
          </ak-form-item>
          <ak-form-item label="">
            <ak-input v-model="dataList.item.label" placeholder="label"></ak-input>
          </ak-form-item>
          <ak-form-item label="" v-if="placeholder">
            <ak-input v-model="dataList.control.placeholder"></ak-input>
          </ak-form-item>
          <ak-form-item label="">
            <ak-input v-model="dataList.control.value"></ak-input>
          </ak-form-item>
          <ak-form-item label="" v-if="tableHeadCheck">
            <ak-checkbox v-model="dataList.tableList"></ak-checkbox>
          </ak-form-item>
          <div v-if="dataList.type==='datePicker'">
            <ak-form-item label="">
              <ak-select v-model="dataList.control.type">
                <ak-option value="year" label="year"></ak-option>
                <ak-option value="month" label="month"></ak-option>
                <ak-option value="date" label="date"></ak-option>
                <ak-option value="datetime" label="datetime"></ak-option>
              </ak-select>
            </ak-form-item>
            <ak-form-item label="">
              <ak-checkbox v-model="dataList.control.readonly"></ak-checkbox>
            </ak-form-item>
          </div>
          <div
            v-if="dataList.type==='radio'||dataList.type==='checkbox'||dataList.type==='select'"
            class="select-option">
            <div class="select-head">
              <h3></h3>
              <ak-tabs :showContent="false" @change="_selectType" v-model="dataList.isFun">
                <ak-tab-pane label="" name="tab-1"></ak-tab-pane>
                <ak-tab-pane label="" name="true"></ak-tab-pane>
              </ak-tabs>
            </div>
            <div v-if="selectTypeName==='tab-1'" class="set-option">
              <div v-for="(opt,index) in selectOptionList" :key="index" class="item">
                <ak-input v-model="opt.label" placeholder=""></ak-input>
                <ak-input v-model="opt.value" placeholder=""></ak-input>
                <i class="icon-del" @click="_delSelectOption(index)"></i>
              </div>
              <ak-button @click="_addSelect" type="primary"></ak-button>
            </div>
            <div v-else class="padding-top">
              <ak-input v-model="dataList.func" placeholder=""></ak-input>
            </div>
          </div>
          <h3></h3>
          <div v-if="dataList.type==='input'">
            <div v-for="(item,index) in dataList.rules" :key="index" class="flex">
              <ak-select :options="rulesOptions" v-model="item.type" :maxHeight="0"></ak-select>
              <ak-input v-model="item.msg" placeholder="" title=""></ak-input>
              <ak-input v-model.number="item.len" placeholder="/"
                        v-show="item.type==='maxLength'||item.type==='minLength'" title="/"></ak-input>
              <ak-input v-model="item.rule" placeholder="" v-show="item.type==='rule'"
                        title=""></ak-input>
              <i class="icon-del" @click="_delRules(index)"></i>
            </div>
            <ak-button @click="_addRules" type="primary"></ak-button>
          </div>
          <div v-else class="required">
            <ak-checkbox v-model="required" @change="_requiredChange"></ak-checkbox>
            <ak-input
              v-model="dataList.rules[0].msg"
              placeholder=""
              v-if="dataList.rules&&dataList.rules[0]">
            </ak-input>
          </div>
        </template>
      </ak-tab-pane>
      <ak-tab-pane label="">
        <div class="padding-top">
          <ak-form-item label="">
            <ak-input v-model="formConfig.name"></ak-input>
          </ak-form-item>
          <ak-form-item label="">
            <ak-input v-model="formConfig.labelWidth"></ak-input>
          </ak-form-item>
          <ak-form-item label="">
            <ak-input v-model="formConfig.className"></ak-input>
          </ak-form-item>
        </div>
      </ak-tab-pane>
    </ak-tabs>
  </div>
</template>

<script>
export default {
  name: 'formControlAttr',
  data() {
    return {
      rulesOptions: [
        {label: '', value: 'required'},
        {label: '', value: 'maxLength'},
        {label: '', value: 'minLength'},
        {label: '', value: 'mobile'},
        {label: '', value: 'tel'},
        {label: '', value: 'phone'},
        {label: '', value: 'mail'},
        {label: '', value: 'digits'},
        {label: '', value: 'number'},
        {label: '', value: 'rule'}
      ],
      required: false,
      selectTypeName: 'tab-1'
    }
  },
  props: {
    formConfig: Object
  },
  components: {},
  methods: {
    _addRules() {
      this.dataList.rules.push({
        type: 'required',
        msg: ''
      })
    },
    _delRules(index) {
      this.dataList.rules.splice(index, 1)
    },
    _requiredChange(val) {
      if (val) {
        this.dataList.rules.push({
          type: 'required',
          msg: ''
        })
      } else {
        this.dataList.rules.splice(0, 1)
      }
    },
    _delSelectOption(index) {
      this.selectOptionList.splice(0, 1)
    },
    _selectType(name) {
      this.selectTypeName = name
      // this.dataList.isFun = name
    },
    _addSelect() {
      this.selectOptionList.push({
        label: '',
        value: ''
      })
    },
    _addTabsLabel() {
      this.dataList.columns.push({
        label: 'Tab' + new Date().getTime(),
        list: []
      })
    },
    _delTabsLabel(index) {
      this.dataList.columns.splice(index, 1)
    }
  },
  watch: {
    dataList: {
      handler: function () {
        const type = this.dataList && this.dataList.rules && this.dataList.rules[0] && this.dataList.rules[0].type
        this.required = type === 'required'
      },
      deep: true
    }
  },
  computed: {
    dataList() {
      const val = this.$store.state.form.controlAttr
      if (Object.keys(val).length === 0) {
        return {item: {}, control: {}}
      }
      return val
    },
    selectOptionList() {
      return this.dataList.control.options || this.dataList.control.data
    },
    tableHeadCheck() {
      return this.$store.state.form.tableHeadCheck
    },
    placeholder() {
      const include = ['input', 'select', 'textarea', 'datePicker']
      return include.indexOf(this.dataList.type) !== -1
    },
    isFormControl() {
      const type = this.dataList.type
      if (type) {
        const include = ['grid', 'childTable', 'tabs']
        return include.indexOf(type) === -1
      }
      return false
    }
  },
  mounted() {
  }
}
</script>
